<template>
  <div>
    <section v-if="showBanner" class="hide-print mb-6">
      <img :src="imgSrc" class="hero" alt="TEDxAuckland 2023" />
    </section>
    <slot />
    <footer class="footer-space"></footer>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  computed: {
    ...mapGetters({
      showBanner: "layouts/showBanner"
    }),
    imgSrc() {
      if (hasOrg(this.$route, "generator")) {
        return "/assets/ticketing-app/images/tedx-2023-banner-generator.png";
      }
      return "/assets/ticketing-app/images/tedx-2023-banner.png";
    }
  }
};

const inPromoCode = (promoCode, orgName) =>
  promoCode && promoCode.toLowerCase().includes(orgName);
const inPath = (path, orgName) => path && path.toLowerCase().includes(orgName);

function hasOrg(route, orgName) {
  const {
    path,
    query: { promoCode }
  } = route;
  return inPromoCode(promoCode, orgName) || inPath(path, orgName);
}
</script>

<style scoped>
.hero {
  width: 100%;
}
</style>
