import NProgress from "nprogress";
import TicketService from "@/services/TicketService";

export const namespaced = true;

export const state = {
  newInvite: {},
  invites: []
};

export const mutations = {
  SET_NEW_INVITE(state, invite) {
    state.newInvite = invite;
  },
  ADD_INVITE(state, invite) {
    state.invites.push(invite);
  },
  SET_INVITES(state, invites) {
    state.invites = invites;
  }
};

export const actions = {
  async getNewInvite({ commit, dispatch }, invite) {
    NProgress.start();
    commit("setLoading", true, { root: true });

    try {
      const res = await TicketService.getNewInvite();
      commit("SET_NEW_INVITE", res.data.invite);

      NProgress.done();
      commit("setLoading", false, { root: true });
    } catch (err) {
      throw err;
    }
  },

  async getInvites({ commit }, ticketOfferId) {
    NProgress.start();
    try {
      const res = await TicketService.getInvites(ticketOfferId);
      commit("SET_INVITES", res.data.invites);

      NProgress.done();
    } catch (err) {
      throw err;
    }
  },

  async addInvite({ commit, dispatch, getters }, invite) {
    NProgress.start();

    const { eventSlug, ticketOffer } = getters;

    invite.ticketOfferId = ticketOffer.id;

    try {
      const res = await TicketService.createInvite(
        eventSlug,
        ticketOffer.id,
        invite
      );
      commit("SET_INVITES", res.data.invites);

      NProgress.done();
    } catch (err) {
      throw err;
    }
  }
};

export const getters = {
  eventSlug(_s, _g, _rs, rootGetters) {
    const slug = rootGetters["events/event"].slug;
    return slug;
  },

  ticketOffer(_s, _g, _rs, rootGetters) {
    const ticketOffer = rootGetters["ticketOffers/ticketOffer"];
    return ticketOffer;
  },

  invites(state) {
    return state.invites;
  },

  purchaseQuantity(_s, getters) {
    return getters.ticketOffer.purchaseQuantity;
  },

  allocatedQuantity(_s, getters) {
    return getters.invites.length + 1;
  },

  allocationLeft(_s, getters) {
    return getters.purchaseQuantity - getters.allocatedQuantity;
  },

  allocationReached(_s, getters) {
    return getters.allocationLeft <= 0;
  }
};
